import React, { Component } from 'react';
import { Col, Form, Input, Select, DatePicker } from 'antd';
import moment from 'moment';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);
@Form.create({ name: 'FormInfoBasic' })
class FormInfoBasic extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = null;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="CMND">
            {getFieldDecorator('system1', { initialValue: '637259173' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ni cp">
            {getFieldDecorator('system2', { initialValue: 'Bnh nh' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ngy cp">
            {getFieldDecorator('system3', { initialValue: moment('2010/10/05') })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Dn tc">
            {getFieldDecorator('system4', { initialValue: 'Kinh' })(createSelect('Dn tc'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tn gio">
            {getFieldDecorator('system5', { initialValue: 'Khng' })(createSelect('Tn gio'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="on">
            {getFieldDecorator('system6', { initialValue: moment('2011/03/26') })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ng">
            {getFieldDecorator('system7', { initialValue: '' })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="T chnh sch">
            {getFieldDecorator('system8', { initialValue: '' })(createSelect('T chnh sch'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="T TC">
            {getFieldDecorator('system9', { initialValue: '' })(createSelect('T TC'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="in thoi nh ring">
            {getFieldDecorator('system10', { initialValue: '0773112' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nhm i">
            {getFieldDecorator('system11', { initialValue: '' })(createSelect('Nhm i'))}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



