import React, { Component } from 'react';
import { Col, Form, Input, Select } from 'antd';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);

@Form.create({ name: 'FormInfoAddrBasic' })
class FormInfoAddrBasic extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      // labelCol: {
      //   xs: { span: 24 },
      //   sm: { span: 6 },
      // },
      // wrapperCol: {
      //   xs: { span: 24 },
      //   sm: { span: 18 },
      // },
      // colon: false,
    };
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Qu qun">
            {getFieldDecorator('system1', { initialValue: 'Bnh nh' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quc tch">
            {getFieldDecorator('system2', { initialValue: 'Vit Nam' })(createSelect('Quc tch'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="TP xut thn">
            {getFieldDecorator('system3', { initialValue: 'Qui Nhn' })(createSelect('Thnh ph'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="C thng tr">
            {getFieldDecorator('system4', { initialValue: '142 Chu Vn An' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="X/Phng">
            {getFieldDecorator('system5', { initialValue: 'Phng 10' })(createSelect('X'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tnh/Tp">
            {getFieldDecorator('system6', { initialValue: 'Qui Nhn' })(createSelect('Tnh'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Qun/Huyn">
            {getFieldDecorator('system7', { initialValue: 'Ph M' })(createSelect('Huyn'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="a ch bo tin">
            {getFieldDecorator('system8', { initialValue: '143 An Dng Vng' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ni  hin nay">
            {getFieldDecorator('system9', { initialValue: '874 Trn Vn ang' })(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



